// content root
.app-content {
  padding-bottom: $app-footer-height;

  &.container {
    padding-right: 0;
    padding-left: 0;
  }

  .has-footer-hidden & {
    padding-bottom: 0;
  }

  .navbar {
    z-index: 100;
  }
}

// content body wrapper
.app-content-inner {
  // position: relative;
  padding: 0 ($grid-gutter-width/2);
  @media (min-width: $grid-float-breakpoint) {
    padding-right: $app-content-padding;
    padding-left: $app-content-padding;
  }
}

// content heading
.content-heading {
  margin-bottom: $line-height-computed;
  border-bottom: 1px solid $border-color;
  @include clearfix;

  + .app-content-inner {
    padding-top: 0;
  }

  .app-content-inner {
    padding-top: 0;
    padding-bottom: 0;
  }

  .breadcrumb-wrapper {
    border-radius: 0;
    border: none;
  }

  .page-header {
    margin: 9px 0;
  }

  hr {
    @include clearfix;
    @media (min-width: $grid-float-breakpoint) {
      margin: 0;
    }
  }
}

// content heading collapse
.content-heading-collapse {
  overflow-x: visible;
  @include clearfix;
  -webkit-overflow-scrolling: touch;

  @media (min-width: $grid-float-breakpoint) {
    width: auto;
    border-top: 0;
    box-shadow: none;

    &.collapse {
      display: block !important;
      height: auto !important;
      padding-bottom: 0; // Override default setting
      overflow: visible !important;
    }

    &.in {
      overflow-y: visible;
    }
  }
}

// content toolbar
.content-toolbar {
  margin: 8px 0;

  @media (min-width: $grid-float-breakpoint) {
    &-left {
      float: left;
    }
    &-right {
      float: right;
    }
  }

  .btn-sm,
  .btn-group-sm,
  .custom-select-sm,
  .form-control-sm {
    margin: 4px;
  }
  .content-breadcrumb {
    margin-top: 0;
    margin-bottom: 0;
  }
}
// content toolbar nav
.content-toolbar-nav {
  margin-bottom: 0;

  @media (min-width: $grid-float-breakpoint) {
    > .form-inline {
      margin-top: 4px;
    }
  }

  > .nav-tabs {
    border-bottom-width: 0;
  }
}

// content breadcrumb
.content-breadcrumb {
  display: inline-block;
  margin: 4px 0;
  padding-right: 0;
  padding-left: 0;
}

// content section
.content-section {
  margin-top: $line-height-computed;
  margin-bottom: $line-height-computed;

  &:last-child {
    margin-bottom: 0;
  }
}

// extra content wrapper
.content-extra {
  position: relative;
  z-index: 2;

  @media (min-width: $grid-float-breakpoint) {
    padding: 0 $spacer;
  }
}
// extra content block
.content-extra-block {
  position: relative;
  padding: $grid-gutter-width;
  background-color: #fff;
  border-width: 0 0 1px 0;
  border-style: solid;
  border-color: $btn-default-border;

  @media (min-width: $grid-float-breakpoint) {
    padding: ($spacer * 2);
    border-width: 0 1px 1px;
    border-bottom-right-radius: $border-radius-base;
    border-bottom-left-radius: $border-radius-base;
  }
}
// extra content tools
.content-extra-tools {
  position: relative;
  z-index: 2;

  .content-heading + & {
    margin-top: -($line-height-computed);
  }

  .btn {
    margin: -1px 1rem 0;
    float: right;
    border-top-width: 0;
    border-top-right-radius: 0;
    border-top-left-radius: 0;

    &:focus {
      outline: none !important;
      background-color: #fff;
    }
  }
}