// app drawer
.drawerjs {
  // only apply when used with perfect scrollbar
  &.drawerjs-has-ps.drawerjs-has-fixed:not(.drawerjs-has-compact) {
    overflow: hidden;
  }

  &.drawerjs-has-fixed {
    &.drawerjs-has-bottom-nav {
      padding-bottom: $app-footer-height;

      .drawerjs-nav-bottom {
        margin-top: 0;
      }
    }
    &.drawerjs-has-navbar {
      padding-top: 51px; // header height
    }

    .navbar-static-bottom,
    .navbar-static-top {
      // isolated navbar with absolute instead of fixed
      position: absolute;
      top: 0;
      width: 100%;
    }
    .navbar-static-bottom {
      top: auto;
      bottom: 0;
    }
  }
  .navbar {
    margin-bottom: 0;
  }

  &.drawerjs-has-compact {
    .drawerjs-inner {
      margin-bottom: 8px;
    }
    .user-avatar .avatar-title,
    .user-avatar .avatar-hint,
    .user-avatar .btn {
      display: none;
    }
  }
}
// app drawer holder
#app-drawer-holder {
  padding-bottom: $line-height-computed;
  border-right: 1px solid #eee;
  border-left: 1px solid #eee;
  min-height: calc(100vh - 93px); // footer + header height
}
// extra drawer
#app-extra {
  z-index: $app-extra-zindex;
}

// remove overflow
.drawerjs.drawerjs-has-open {
  // overflow: visible !important;
}

// drawerjs-section
.drawerjs-section {
  > .panel {
    border-radius: 0;
  }

  > .list-group {
    > .list-group-item {
      border-radius: 0;
      border-right-width: 0;
      border-left-width: 0;
      background-color: transparent;
    }
  }
}

// smart compact toggler icon
.icon-toggle-compact {
  display: inline-block;
  font: normal normal normal 14px/1 FontAwesome;
  font-size: inherit;
  text-rendering: auto;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  .drawerjs-has-right.drawerjs-has-compact &:before,
  &:before {
    content: "\f053";
  }
  .drawerjs-has-right &:before,
  .drawerjs-has-compact &:before {
    content: "\f054";
  }
}

// drawerjs-nav
.drawerjs-nav {
  &.nav > li > a {
    color: $stacked-menu-color;
    &:hover,
    &:focus {
      background-color: transparent;
      color: $stacked-menu-active-color;
    }

    > .icon-toggle-compact {
      position: relative;
      top: 3px;
    }
  }
}
.drawerjs-has-fixed {
  .drawerjs-nav.nav-right {
    text-align: center;
     > li {
      float: none;
      display: inline-block;
    }
  }
}
.drawerjs-has-compact {
  .drawerjs-nav {
    float: none;

    &.nav-right > li {
      float: none;
    }
    > li {
      display: block;
      text-align: center;
      > a {
        margin: 0;
        float: none !important;
      }
    }
  }
}

// drawerjs-navbar
.drawerjs-navbar {
  margin-bottom: 0;
  background-color: transparent;
  border-width: 0 0 1px;
  border-radius: 0;

  .drawerjs-nav > li > a {
    padding-top: 14px;
    padding-bottom: 14px;
  }

  .drawerjs-has-compact & {
    .navbar-brand {
      display: block;
      float: none;
      text-align: center;
    }
  }
}

.drawerjs-nav-bottom {
  margin-top: 1rem;
}

// has layout floated than increase the zindex
.has-layout-floated .app-drawer {
  z-index: 1040;
}

// drawer menu
.menu-header > h2 {
  margin: 0;
  padding: 0;
  font-size: inherit;
  font-weight: 700;
  line-height: inherit;
}

// variant size of menus
.menu > .menu-item-slim > .menu-link,
.stacked-menu-has-compact > .menu > .menu-item:not(.has-child) > .menu-link .menu-text {
  padding-top: .25rem;
  padding-bottom: .25rem;
}