body {
  position: relative;
  overflow-x: hidden;
}
.app-main {
  // header space
  .has-layout-fixed &,
  .has-header-fixed & {
    padding-top: 51px; // header height
  }
}

// give app drawer padding bottom
.has-layout-fixed {
  .app-drawer {
    height: auto;
  }
}
// then remove when app has footer fixed
.has-footer-fixed {
  .app-drawer {
    bottom: $app-footer-height;
  }
  &.has-footer-hidden .app-drawer {
    bottom: 0;
  }
}

// increase zindex header
.has-layout-cliped,
.has-layout-fixed,
.has-header-fixed {
  // zIndex must greater than drawer
  .app-header {
    z-index: $app-header-fixed-zindex;
  }
}

// disable transition on scroll
.has-scrolled {
  .app-header,
  .app-content,
  .app-drawer {
    transition: none;
  }
}