.app-footer {
  padding: $app-footer-padding;
  position: absolute;
  right: 0;
  bottom: 0;
  left: 0;
  height: $app-footer-height;
  background-color: $app-footer-bg;
  color: $app-footer-text-color;
  border: 1px solid $app-footer-border-color;
  overflow: hidden;
  transition: right 150ms, left 150ms, transform 150ms;

  .has-footer-fixed & {
    z-index: $app-footer-zindex;
  }
}

.app-footer-inner {
  padding: 0 ($grid-gutter-width/2);
  &.container {
    padding-right: 0;
    padding-left: 0;
  }
  @media (min-width: $grid-float-breakpoint) {
    padding-right: $app-footer-inner-padding;
    padding-left: $app-footer-inner-padding;
  }
}

.app-footer-fixed {
  position: fixed;
}

// inverse background on fixed and boxed layout
.app-footer-fixed {
  background-color: $app-footer-inverse-bg;
  color: $app-footer-inverse-text-color;
  border-color: $app-footer-inverse-bg;
}
.has-layout-boxed .app-footer {
  @media (min-width: $container-sm) {
    background-color: $app-footer-inverse-bg;
    color: $app-footer-inverse-text-color;
    border-color: $app-footer-inverse-bg;
  }
}