// content table view
.table-view {
  margin-right: -($app-content-padding);
  margin-left: -($app-content-padding);

  .table-responsive {
    border-width: 0 0 1px;
  }
  .table {
    tr > th.table-tools,
    tr > td.table-tools {
      overflow: visible;
    }
  }

  @media (min-width: $grid-float-breakpoint) {
    margin-right: 0;
    margin-left: 0;
  }
}

.table-tools {
  overflow: initial;

  .nav {
    margin-bottom: 0px;
    > li > a {
      padding-top: 0;
      padding-bottom: 0;
    }
  }
}
.table-view-details {
  position: absolute;
}