.app-header {
  position: relative;
  transition: transform 150ms;
  z-index: $app-header-zindex;

  .navbar {
    margin-top: 0;
    margin-bottom: 0;
  }
}

.navbar .global-search {
  width: auto;

  @media (min-width: $screen-sm) {
    width: 240px;
  }
  @media (min-width: $screen-md) {
    width: 300px;
  }
}

.app-header-fixed,
.has-layout-fixed .app-header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
}

.has-layout-cliped {
  @media (min-width: $screen-md) {
    .app-header .navbar-header {
      min-width: $drawerjs-width - $grid-gutter-width;
    }
  }
}