// content boards
.board {
  margin-right: -($grid-gutter-width/2);
  margin-left: -($grid-gutter-width/2);

  @media (min-width: $grid-float-breakpoint) {
    margin-right: -($app-content-padding);
    margin-left: -($app-content-padding);
  }
}
.board-inner {
  width: 100%;
  overflow-x: scroll;
  white-space: nowrap;
  padding-right: $grid-gutter-width/2;
  padding-left: $grid-gutter-width/2;

  @media (min-width: $grid-float-breakpoint) {
    padding-right: $app-content-padding;
    padding-left: $app-content-padding;
    min-height: 938px;
    // header height + content heading height + content hading margin
    min-height: calc(100vh - 126px);
  }

  .has-layout-fixed &,
  .has-footer-fixed & {
    @media (min-width: $grid-float-breakpoint) {
      // header height + content heading height + content hading margin + footer height
      min-height: calc(100vh - 167px);
    }
  }
}

.board-stage {
  display: inline-block;
  min-width: 260px;
  max-width: 400px;
  white-space: normal;
  vertical-align: top;

  + .board-stage {
    margin-left: $grid-gutter-width/2;

    @media (min-width: $grid-float-breakpoint) {
      margin-left: $app-content-padding;
    }
  }
}

.stage-list {
  padding: 10px;
}

.stage-panel {
  display: block;
  margin-bottom: 10px;
  padding: 10px;
  color: $text-color;
  cursor: pointer;

  &:last-child {
    margin-bottom: 0;
  }
}