// App helper classes come with simple show/hide an element
// helper layout floated scope
.has-layout-floated {
  .hide-on-layout-floated {
    display: none;
  }
  .show-on-layout-floated {
    display: block;
  }
  .show-inline-on-layout-floated {
    display: inline-block;
  }
}

