// reference-details content
#app-drawer-holder {
  padding-bottom: 0;
}
.reference-details {
  position: relative;
  margin-right: -($grid-gutter-width/2);
  margin-left: -($grid-gutter-width/2);
  min-height: 603px;
  height: 603px;
  height: calc(100vh - 51px); // header height
  background-color: $body-bg;

  @media (min-width: $screen-md) {
    overflow: hidden;
  }
  @media (min-width: $grid-float-breakpoint) {
    margin-right: -($grid-gutter-width);
    margin-left: -($grid-gutter-width);
  }
  &.has-open-content {
    .reference-list {
      z-index: 1;
    }
    .reference-content {
      z-index: 2;
    }
  }

  .has-layout-fixed & {
    min-height: 585px;
    height: 585px;
    height: calc(100vh - 93px); // header + footer height
  }
  .has-footer-hidden.has-layout-fixed & {
    min-height: 603px;
    height: 603px;
    height: calc(100vh - 51px); // header height
  }
}

.reference-list,
.reference-content {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: inherit;

  &.has-navbar {
    > .navbar {
      margin-bottom: 0;
    }
    > .reference-inner {
      top: 51px;
    }
  }
  &.has-nav {
    > .reference-inner {
      top: 43px;
    }
  }
}
.reference-list {
  width: 100%;
  z-index: 2;
  @media (min-width: $screen-md) {
    right: auto;
    width: 360px;
    border-right: 1px solid darken($border-color, 2%);
    &-right {
      right: 0;
      left: auto;
      border-right: 0;
      border-left: 1px solid darken($border-color, 2%);
    }
  }
  .reference-inner {
    padding: 0;
  }
}
.reference-content {
  z-index: 1;
  @media (min-width: $screen-md) {
    .has-side-left & {
      left: 360px;
    }
    .has-side-right & {
      right: 360px;
    }
  }
}

.has-side-right-closed {
  .reference-list:last-child {
    display: none;
  }
  .reference-content {
    @media (min-width: $screen-md) {
      right: 0;
    }
  }
}

.has-side-right-closed {
  .reference-list:first-child {
    display: none;
  }
  .reference-content {
    @media (min-width: $screen-md) {
      left: 0;
    }
  }
}

// wrapper
.reference-inner {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  padding: ($grid-gutter-width/2);
  overflow-y: auto;
  @media (min-width: $grid-float-breakpoint) {
    padding: $grid-gutter-width;
  }

  &.has-loading {
    position: absolute;
  }
}