// content side
.content-side-wrapper {
  @media (min-width: $screen-sm-max) {
    display: block !important;
  }
}
.content-side {
  margin-top: $line-height-computed;
  margin-bottom: $line-height-computed;
}
.content-side-affix {
  @media (min-width: $screen-sm-max) {
    &.affix-top {
      padding-right: $grid-gutter-width;
    }
    &.affix {
      position: fixed;
      top: 0;
    }
  }
}
.has-layout-fixed,
.has-header-fixed {
  .content-side-affix {
    @media (min-width: $screen-sm-max) {
      &.affix {
        top: 51px; // header height
      }
    }
  }
}